<template>
    <scroll-view class="scroll-view" :style="{backgroundColor: tabBgColor}" scroll-x="true" :show-scrollbar="false" :scroll-into-view="id"
        :scroll-with-animation="true">
        <view class="tab" :style="{width: (width * tabs.length) + 'rpx'}">
            <view :id="index | getId" class="tab-item" :style="{width: width + 'rpx'}" v-for="(item, index) of tabs"
                :key="index" @tap="changeTab(index)">
                <view class="tab-item-text" :style="{color: ((index == idx) ? $store.state.skin.SKIN_BG_COLOR : tabDefaultFontColor)}">{{item}}</view>
            </view>
            <view class="bd" :style="{
						width: 40 + 'rpx',
						marginLeft: (width - 40) / 2 + 'rpx',
						transform: 'translateX(' + (width * idx) + 'rpx)',
                        backgroundColor: $store.state.skin.SKIN_BG_COLOR
					}">
            </view>
        </view>
    </scroll-view>
</template>

<script>
    export default {
        name: "sj-tab-bar",
        data() {
            return {
                idx: 0,
                id: 'item_0'
            };
        },

        props: {
            // tabbar的数据
            tabs: {
                type: Array,
                default: () => []
            },
            
            // 外部传入的下表
            current: {
                type: [String, Number],
                default: ''
            },

            // tab背景色
            tabBgColor: {
                type: String,
                default: '#fff'
            },

            // 默认文本颜色
            tabDefaultFontColor: {
                type: String,
                default: '#333'
            },

            // 选中文本颜色
            tabSelectedFontColor: {
                type: String,
                default() {
                    return this.$store.state.skin.SKIN_BG_COLOR
                }
            },

            // 选中滑块颜色
            tabSelectedBorderColor: {
                type: String,
                default() {
                    return this.$store.state.skin.SKIN_BG_COLOR
                }
            }
        },
        
        created() {
            if(this.current !== ''){
                this.idx = this.current
                this.id = 'item_' + this.current
            }
        },
        
        watch: {
            current(val) {
                this.idx = this.current
                this.id = 'item_' + this.current
            }
        },

        computed: {
            // 计算tab每一项的宽度
            width() {
                const len = this.tabs.length
                return len <= 5 ? (750 / len) : 160
            }
        },

        filters: {
            getId(val) {
                return 'item_' + val
            }
        },

        methods: {
            // 点击tab每一项
            changeTab(index) {
                if(index === this.idx) return
                this.idx = index
                this.id = 'item_' + index
                this.$emit('changeTab', index)
            }
        }
    }
</script>

<style lang="less" scoped>
    .scroll-view{
        width: 750rpx;
        height: 84rpx;
    }
    .tab {
        .flexbox();
        position: relative;
        height: 84rpx;
    }

    .tab-item-text {
        text-align: center;
        line-height: 80rpx;
        transition: color .3s;
        font-size: 30rpx;
    }

    .bd {
        height: 4rpx;
        position: absolute;
        bottom: 8rpx;
        transition: transform .3s;
    }
</style>
